.quickview {
  //border: solid 2px black;
  // width: 30%;
  // height: 50%;

  width: 430px;
  height: 580px;

  margin: 0 auto;
  // margin-top: 2%;

  background: #ffffff;
  border-radius: 15px;
  box-shadow: 2px 2px 40px rgba(0, 35, 35, 0.05);
  backdrop-filter: blur(30px);
  position: relative;
}
.enable {
  color: #394759 !important;
  P {
    color: #394759 !important;
  }
}
.enable:hover {
  cursor: pointer;
  transform: scale(2.2) !important;
  z-index: 10 !important;
  opacity: 1 !important;
  filter: blur(0) !important;
}
